interface Args {
  baseId: string,
  tableId: string,
  token: string
}

interface Options {
  filter?: string,
  sort?: {
    field: string,
    direction: 'asc' | 'desc'
  }[],
  max?: 1 | 2 | 100 | number
}

interface Data {
  records?: {
    id: string,
    createdTime: Date,
    fields: {}
  }[],
  error?: {
    type: string,
    message: string
  }
}

export default class AirtableFetcher {
  private baseId: string
  private tableId: string
  private accessToken: string

  constructor({ baseId, tableId, token }: Args) {
    this.baseId = baseId
    this.tableId = tableId
    this.accessToken = token
  }

  async get({ filter, sort, max }: Options): Promise<Data> {
    const res = await fetch(
      `https://api.airtable.com/v0/${this.baseId}/${this.tableId}/listRecords`,
      {
        method: 'POST',
        headers: {
          'Accept': 'Application/json',
          'Content-Type': 'Application/json',
          Authorization: `Bearer ${this.accessToken}`
        },
        body: JSON.stringify({
          filterByFormula: filter || '',
          sort: sort || [],
          maxRecords: max || 100
        })
      },
    )

    const data: Data = await res.json()

    return data
  }
}